import { Component, OnInit } from '@angular/core';
import { IMatch } from '../../models/match';
import { IDeliveries } from '../../models/deliveries';
import { AppServices } from '../../services/appservices.service';
import 'rxjs/add/operator/toPromise';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  matchlist: IMatch[];
  livematch: IMatch;
  lastdelivery: IDeliveries;
  inning1score: String;
  inning2score: String;
  currentOver: String;

  constructor(private _appService: AppServices) { }

  ngOnInit() {
    this._appService.getMatchList().subscribe(result => {
      this.matchlist = result;

      if (!localStorage.getItem("lmatch")) {
        let rid = Math.floor((Math.random() * 101) + 450);
        localStorage.setItem("lmatch", JSON.stringify(result[rid]));
        this.livematch = result[rid];
      }
      else {
        this.livematch = JSON.parse(localStorage.getItem("lmatch"));
      }

      this.getlivematchdata();
      let that = this;
      setInterval(function () { that.getlivematchdata() }, 5000);

    });
  }

  getlivematchdata() {

    let match = JSON.parse(localStorage.getItem("lmatch"));
    let mdata = localStorage.getItem("lmatchdata");
    let mwickets1 = localStorage.getItem("lmatchwickets1")
    let mruns1 = localStorage.getItem("lmatchruns1");
    let mwickets2 = localStorage.getItem("lmatchwickets2")
    let mruns2 = localStorage.getItem("lmatchruns2");
  
    if (mdata) {
      let mdatai = mdata.split("|");
      this._appService.getlatdelivery(mdatai[0], mdatai[1], mdatai[2], mdatai[3]).toPromise().then(resultinit => {
        let result = resultinit[0];
        this.lastdelivery = result;

        if (result.inning === "1") {
          localStorage.setItem("lmatchruns1", "" + (parseInt(mruns1) + parseInt(result.total_runs)));
          if (result.player_dismissed)
            localStorage.setItem("lmatchwickets1", "" + (parseInt(mwickets1) + 1));

          this.inning1score = localStorage.getItem("lmatchruns1") + "/" + localStorage.getItem("lmatchwickets1");
          this.inning2score = "0/0";
        }
        else {
          localStorage.setItem("lmatchruns2", "" + (parseInt(mruns2) + parseInt(result.total_runs)));
          if (result.player_dismissed)
            localStorage.setItem("lmatchwickets2", "" + (parseInt(mwickets2) + 1));


          this.inning2score = localStorage.getItem("lmatchruns2") + "/" + localStorage.getItem("lmatchwickets2");
        }

        this.currentOver = result.over + "." + result.ball;

        let str = "";
        if (mdatai[3] == result.inning) {
          str = result.match_id + "|";
          str += result.over + "|";
          str += result.ball + "|";
          str += result.inning;
        }
        else {
          str = result.match_id + "|";
          str += 1 + "|";
          str += 0 + "|";
          str += result.inning;
        }
        localStorage.setItem("lmatchdata", str);

      });
    }
    else {

      localStorage.setItem("lmatchwickets1", "0");
      localStorage.setItem("lmatchruns1", "0");
      localStorage.setItem("lmatchwickets2", "0");
      localStorage.setItem("lmatchruns2", "0");

      this._appService.getlatdelivery(match["id"], 1, 0, 1).toPromise().then(resultinit => {
        let result = resultinit[0];
        this.lastdelivery = result;

        if (result.inning === "1") {
          localStorage.setItem("lmatchruns1", "" + (parseInt(0 + result.total_runs)));
          if (result.player_dismissed)
            localStorage.setItem("lmatchwickets1", "" + 1);

          this.inning1score = localStorage.getItem("lmatchruns1") + "/" + localStorage.getItem("lmatchwickets1");
          this.inning2score = "0/0";
        }
        else {
          localStorage.setItem("lmatchruns2", "" + (parseInt(0 + result.total_runs)));
          if (result.player_dismissed)
            localStorage.setItem("lmatchwickets2", "" + 1);

          this.inning2score = localStorage.getItem("lmatchruns2") + "/" + localStorage.getItem("lmatchwickets2");
        }

        this.currentOver = result.over + "." + result.ball;

        let str = result.match_id + "|";
        str += result.over + "|";
        str += result.ball + "|";
        str += result.inning;

        localStorage.setItem("lmatchdata", str);

      });
    }
  }

  

}



