import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';
import { Observable } from 'rxjs/Rx';
import { Http } from '@angular/http';
import { IMatch } from '../models/match';
import { IDeliveries } from '../models/deliveries';

@Injectable()
export class AppServices {

  constructor(private http: Http) { }

  getMatchList(): Observable<IMatch[]> {
    return this.http.get("/api/matches")
      .map(res => res.json());
  }

  getlatdelivery(matchid, over, ball, inning): Observable<IDeliveries> {

    if (parseInt(ball) === 6) {
      over = parseInt(over) + 1; ball = 1;
    }
    else {
      ball = parseInt(ball) + 1;
    }

    let mwickets1 = localStorage.getItem("lmatchwickets1")
    if (parseInt(over) == 21 || mwickets1 == "10") {
      over = 1; ball = 1;
    }

    return this.http.get("/api/deliveries/" + matchid + "/" + over + "/" + ball + "/" + inning)
      .map(res => res.json());
  }

}



